  transition: 0.2s;
  border-width: 1px;
  padding: 1px;
}

.sample_container.card_sample .e-custom-cards {
  position: relative;
  margin-top: 80px;
  overflow: visible;
  border-radius: 15px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  transition: 0.2s;
  border-width: 1px;
  padding: 1px;
}

body.bootstrap-dark .e-custom-card .avatar-content {
  color: #ffffff;
}

.tailwind .e-custom-card .e-avatar,
